.flex{
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.outer{
    margin-top: 50px;
    width: 900px;
    font-size: 18px;
}
#container{
    width: 900px;
    background-color: rgb(252, 247, 195);
}
.minidiv{
    background-color: rgb(247, 210, 0);
    border: solid 3px rgb(87, 74, 0);
    height: 100px;
    width: 100px;
    margin: 80px 20px 80px 20px;
    text-align: center;
    line-height: 100px;
}
.button{
    outline: none;
    border: 1px rgb(252, 251, 242) solid;
    background-color: rgb(87, 74, 0);
    height: 50px;
    width: 150px;
    margin: 30px 0 40px 0;
    font-size: 18px;
    color: white;
}
.img{
    width: 900px;
    margin-top: 40px;
}
